* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'montserrat', sans-serif;
  background-color: #011121;
}

header {
  padding: 50px 15px 0px;
}

header h1 {
  color: #FFF;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
}

main {
  width: 100%;
  max-width: 960px;
  margin: 0 auto;
}

.searchbox-wrapper {
  position: relative;
  margin-top: 50px;;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  transition: all 0.5s;
  width: 50px;
  height: 50px;
  background: white;
  box-sizing: border-box;
  border-radius: 25px;
  border: 4px solid white;
  padding: 15px;
}

.searchbox {
  position: absolute;
  top: 0;
  width: 100%;
  height: 42.5px;
  line-height: 30px;
  outline: 0;
  border: 0;
  display: none;
  font-size: 1em;
  border-radius: 20px;
  padding: 0 20px;
  left: 0;
}

.searchbox-wrapper:hover {
  width: 600px;
}

.searchbox-wrapper:hover .searchbox {
  display: block;
}

.search-icon {
  padding: 10px;
  width: 42.5px;
  height: 42.5px;
  position: absolute;
  top: 0;
  right: 0;
  border-radius: 50%;
  color: #07051a;
  text-align: center;
  font-size: 1.2em;
  transition: all 1s;
  box-sizing: border-box;
}

.searchbox:focus {
  box-shadow: 0px 0px 8px 3px #4484C4;
}

.search-results {
  display: flex;
  flex-wrap: wrap;
  
}

.search-results .results{
  width: 25%;
  min-width: 12vw;
  padding: 15px;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}

.search-results .results img {
  display: block;
  width: 100%;
  border-radius: 10px;
}


.search-results .results h3 {
  color: #FFF;
  font-size: 14px;
  font-weight: 600;
  padding: 10px;
  background: #445565;
  flex: 1 1 10%;
  transition: 0.4 ease-out;
  text-align: center;
  border-radius: 10px;
  margin-top: 5px;
  max-height: 48px;
}

.search-results .results:hover h3 {
  background: #FFF;
  color: #223343;
}

.popup {
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  padding: 1%;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: #1D1E22;  
}

.popup .poster-wrapper {
  /* background-color: red; */
  display: flex;
  width: 17vw;
  height: 50vh;
  margin: 0px 0px 0px 9.5vw;
  color: #FFF;
  border-radius: 10px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  position: relative;
  overflow: hidden;
  z-index: 1;
  background: inherit;
}

.popup .poster-wrapper:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 50px rgba(255, 255, 255, .5);
  filter: blur(8px);
  background: inherit;
  
  z-index: -1;
}

.popup .poster-wrapper .poster {
  max-width: 300px;
  height: 45vh;
  margin-left: 1.3em;
  align-self: center;
  border-radius: 8px;
  box-shadow: 1px 2px 10px #000;
}

.popup .content-info {
  /* background-color: blue; */
  display: flex;
  flex-direction: column;
  color: #FFF;
  width: 60vw;
  height: 50vh;
  padding: 1vw;
  margin-left: 1%;
  border-radius: 10px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  position: relative;
  overflow: hidden;
  z-index: 1;
  background: inherit;
}

.popup .content-info:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 50px rgba(255, 255, 255, .5);
  filter: blur(8px);
  background: inherit;
  margin: -20px;
  z-index: -1;
}

.popup .content-info h1 {
  margin: 30px 0px 20px 0px;
  text-shadow: 1px 1px 10px #000;
  font-size: 2.5rem;
}

.popup .content-info .rating {
  font-size: 1.5rem;
  text-shadow: 1px 1px 10px #000;
  font-weight: 600;
  width: 10vw;
  height: 7.2vh;
}

.popup .content-info .genre {
  
  font-size: 1.5em;
  text-shadow: 1px 1px 10px #000;
  width: 10vw;
  height: 7.2vh;
}

.popup .content-info .plot {
  font-size: 1.2rem;
  text-shadow: 1px 1px 10px #000;
  margin-top: 5%;
}

.popup .cast {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  width: 60vw;
  height: 22vh;
  margin: -10% 0px 0px 9.5%;
  border-radius: 10px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  position: relative;
  overflow: hidden;
  z-index: 1;
  background: inherit;
}

.popup .cast:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 50px rgba(255, 255, 255, .5);
  filter: blur(8px);
  background: inherit;
  margin: -20px;
  z-index: -1;
}

.popup .cast .cast-wrap {
  display: flex;
}

.popup .cast .cast-wrap .castimg {
  display: inline-block;
  
}

.popup .cast .cast-wrap .castimg img{
  width: 4vw;
  height: 9vh;
  margin: 1vw 4vw 0vw 4vw;
  border-radius: 50%;
  box-shadow: 1px 1px #000;
}

.popup .cast .cast-wrap .castimg p{
  font-weight: 600;
  font-size: 1vw;
  margin: 1vw -1vw;
  color: #FFF;
  text-align: center;
  text-shadow: 1px 1px 10px #000;
}


.popup .cast .castname-wrap {
  background-color: lawngreen;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  font-weight: 600;
  color: #FFF;
}

.popup .cast .castname-wrap .name{
  margin: 5px ;
  list-style-type: none;
}

.popup .runtime {
  width: 17.2vw;
  height: 22vh;
  margin: -10% 0px 0px 1rem;
  border-radius: 10px;
  box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  position: relative;
  overflow: hidden;
  z-index: 1;
  background: inherit;
}

.popup .runtime:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  box-shadow: inset 0 0 50px rgba(255, 255, 255, .5);
  filter: blur(8px);
  background: inherit;
  margin: -20px;
  z-index: -1;
}

.popup .runtime p {
  color: #FFF;
  font-size: 1.2vw;
  text-shadow: 1px 1px 10px #000;
  margin-top: 2%;
  margin-right: 5%;
  padding: 4%;
  text-align: center;
}

.info-icon {
  position: relative;
  margin-right: 5%;
  top: 5px;
  left: 5px;
}


.close {
  position: absolute;
  background-color: cornflowerblue;
  border-radius: 8px;
  border: none;
  box-shadow: 2px 2px black;
  width: 100px;
  height: 30px;
}

@media all and (max-width: 500px) {
  * {
    color: red;
  }

  section {
    background-size: contain;
    background-attachment: fixed;
  }

  .popup {
    width: 100%;
    display: flex;
    flex-direction: column;
    background-size: 100%;
    overflow-x: scroll;
  }

  .search-results {
    flex-wrap: wrap;
  }

  .search-results .results {
    width: 45vw;
    margin: 0% 0% 0% 5%;
  }

  .search-results .results img {
    width: 30vw;
  }

  .search-results .results h3 {
    font-size: 0.7rem;
    width: 30vw;
  }
  .searchbox-wrapper:hover {
    width: 300px;
  }
  .popup .content-info {
    width: 92vw;
    flex: auto;
    position: absolute;
    margin-top: 60vh;
    margin-left: 3%;
    
    overflow: hidden;
    z-index: 1;
    background: inherit;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2);
  }

  .popup .content-info:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 50px rgba(255, 255, 255, .5);
    filter: blur(8px);
    background: inherit;
    margin: -20px;
    z-index: -1;
  }

  .popup .content-info h1 {
    font-size: 1rem;
  }

  .popup .content-info .rating {
    font-size: 1rem;
  }

  .popup .content-info .genre {
    font-size: 1rem;
  }

  .popup .content-info .plot {
    font-size: 1rem;
  }
  .popup .cast {width: 92vw; color: red; position: absolute; margin-top: 111vh; margin-left: 3%;}
  .popup .runtime{width: 92%; position: absolute; margin-top: 134vh; margin-left: 3%; }
  .popup .poster-wrapper {
    width: 61%;
    margin: 17%;
  }


}

@media all and (max-width: 1300px) {

  .popup .content-info .genre {
    font-size: 1rem;
  }

  .popup .content-info .plot {
    font-size: 0.8rem;
  }
}